ul {
    list-style-type: none;
}

html {
    scroll-behavior: smooth;
}

body {
    padding: 0;
    margin: 0;
}

.nav {
    padding: 1.83% 1%;
    font-size: large;
}

.usernav {
    padding: 1.25% 1% 1.1% !important;
    font-size: large;
}

.logonav {
    padding: 1.67% 1%;
    font-size: large;
}

.logonav:hover {
    background-color: Tomato !important;
    color: white;
    transition: background-color 0.5s;
    border-radius: 8px;
}

.navbar {
    box-sizing: border-box;
    background-color: azure;
    border: 1px solid rgba(17, 204, 85, 0.133);
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1;
    box-shadow: 0px 2px 10px rgb(243, 97, 121);
}

li>a {
    text-decoration: none;
    color: black;
    font-weight: bold;
}

li>a:hover {
    color: white !important;
    background-color: Tomato !important;
    transition: background-color 0.5s !important;
}

.nav:hover {
    background-color: Tomato !important;
    color: white;
    transition: background-color 0.5s;
    border-radius: 8px;
}

.navWidth {
    text-align: center;
    height: -webkit-fill-available;
}

.imgprop {
    width: -webkit-fill-available;
    height: -webkit-fill-available;
    vertical-align: bottom;
}

#topdiv {
    height: 120vh;
    background-size: cover !important;
    background-repeat: no-repeat;
    border-top: 1px solid black;
}

.spin {
    text-align: center;
}

.topdivabout {
    height: 65vh;
}

.slider {
    margin-top: -18vh;
}

.slidertitle {
    text-align: center;
    font-size: x-large;
    color: white;
}

.sliderdesc {
    text-align: center;
    box-sizing: border-box;
    font-size: large;
    color: white;
    padding-bottom: 2%;
}

#title1 {
    width: 70vw;
    margin-top: 15vh;
    padding-top: 2vh;
    color: Tomato;
    text-align: center;
    font-size: -webkit-xxx-large;
}

.abouttitle {
    text-align: center;
    font-size: xx-large;
    color: white;
}

.aboutdesc {
    text-align: center;
    box-sizing: border-box;
    font-size: large;
    color: white;
}

#title2 {
    margin-top: -4vh;
    color: Tomato;
    text-align: center;
    font-size: xx-large;
}

#joinnow {
    width: 100vw;
    margin-top: 4vh;
    color: white;
    float: right;
    text-align: center;
}

#joinlink {
    color: Tomato;
    background-color: transparent;
    border: 2px solid Tomato;
    padding: 0.5% 1%;
    border-radius: 50px;
    text-decoration: none;
    font-size: xx-large;
}

#joinlink:hover {
    background-color: tomato;
    color: white !important;
    transition: color 0.5s, background-color 0.5s;
    border: 2px solid;
}

#usericon {
    position: absolute;
    top: -3.5vh;
    right: -6vh;
}

#username {
    position: absolute;
    top: -2vh;
    right: -15vh;
}

.section2nd {
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    position: absolute;
    left: 50%;
    top: 50%;
    Transform: translate(-50%, -50%);
    font-size: larger;
}

.section {
    width: 50%;
    overflow: hidden;
}

.sectionimg {
    height: 100%;
    width: 100%;
    display: block;
    margin: auto;
    cursor: zoom-in;
    background-color: hsl(0, 0%, 90%);
    transition: background-color 300ms;
}

.sectionlink {
    background-color: #C4306B;
    color: white;
    text-decoration: none;
    border: 1px solid #C4306B;
    padding: 3% 5%;
    border-radius: 25px;
}

.sectionlink:hover {
    border: 1.5px solid black;
    color: black;
    background-color: transparent;
    transition: color 0.5s, background-color 0.5s;
}

.sectionimg:hover {
    transform: scale(1.2);
    transition: transform 0.5s;
}

.socialicon {
    padding: 0 1%;
    font-size: 33px;
}

.imput {
    margin-top: 1.5%;
}

.calculatebtn {
    padding: 1% 2%;
    font-size: inherit;
    background-color: #E43D3D;
    color: white;
    box-shadow: 1px 1px 6px #E43D3D;
    border: 1px solid #E43D3D;
    border-radius: 25px;
}

.calculatebtn:hover {
    border-color: #2423e6;
    background-color: #2423e6;
    box-shadow: 1px 1px 6px #2423e6;
    color: white;
    transition: color 0.5s, background-color 0.5s, border-color 0.5s;
}

.submit {
    padding: 1% 2%;
    font-size: inherit;
    background-color: tomato;
    color: white;
    box-shadow: 1px 1px 6px tomato;
    border: 1px solid tomato;
    border-radius: 25px;
}

.submit:hover {
    border-color: black;
    background-color: black;
    box-shadow: 1px 1px 6px black;
    color: white;
    transition: color 0.5s, background-color 0.5s;
}

.nameinputs {
    font-family: 'Inter';
    height: 30px;
    outline: none;
    width: 100%;
    background-color: transparent;
    font-size: 18px;
    border: none;
    border-bottom: 2px solid mediumpurple;
}

.nameinputs:hover {
    border-bottom: 2px solid rgb(255, 135, 155);
}

.otherinputs {
    font-family: Inter;
    height: 30px;
    outline: none;
    background-color: transparent;
    font-size: 18px;
    width: 75%;
    border: none;
    border-bottom: 2px solid mediumpurple;
}

.otherinputs:hover {
    border-bottom: 2px solid rgb(255, 135, 155);
}

.symbol {
    font-size: xxx-large;
    padding: 0 5%;
    font-weight: bolder;
    text-align: center;
    color: cadetblue;
    border: 1px solid cadetblue;
    border-radius: 6px;
    box-shadow: 1px 1px 6px #4a5b5c;
    top: 50%;
    cursor: pointer;
    position: absolute;
    transform: translate(30%, -50%);
}

.sliderimages {
    width: 100%;
}

.sliderimages:hover {
    transform: scale(1.25);
    transition: transform 0.7s;
}

.membershipcard {
    padding: 2%;
    color: black;
    box-sizing: border-box;
    width: 30%;
    text-align: center;
    border: 1px solid #c6c5c5;
    background-color: azure;
    border-radius: 8px;
}

.membershipHeading {
    font-size: xx-large;
    font-family: sans-serif;
}

.subscriptionPeriod {
    font-size: larger;
    font-weight: bolder;
    margin-top: -5%;
}

.subscriptionStart {
    font-size: x-large;
    padding: 2% 6%;
    border-radius: 5px;
    background-color: black;
    color: white;
}

.subscriptionlist {
    padding: 1% 0;
    font-size: larger;
}

.aboutScription {
    font-size: large;
    padding: 0 9%;
    margin-bottom: 6%;
}

.serviceNav {
    display: flex;
    font-size: x-large;
    justify-content: space-between;
    font-weight: lighter;
    box-sizing: border-box;
    padding: 0 20% 1%;
    background-color: azure;
}

.servicelinks {
    width: auto;
    text-align: center;
    padding: 3% 0 2%;
}

.sublinks:hover {
    background-color: azure !important;
    color: black !important;
}

.serviceheading {
    background-color: lightgreen;
    color: white;
    margin: 2% 20%;
    padding: 0.7%;
    border-radius: 5px;
}

.gender {
    display: flex;
    text-align: left;
    box-sizing: border-box;
    padding: 3.5% 0 2% 10%;
}

.bmiform {
    text-align: left;
    font-size: x-large;
    padding: 3% 0px;
}

.bmiinputs {
    height: 30px;
    width: 100%;
    text-align: center;
    outline: none;
    background-color: transparent;
    font-size: 17px;
    border: none;
    border: 2px solid #56B9EE;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.gotoTop {
    position: fixed;
    z-index: 1;
    bottom: 4%;
    right: -7%;
    justify-content: end;
    width: 15%;
    animation: topbutton 1.5s infinite;
}

.gotoTop:hover {
    animation: none;
}

@keyframes topbutton {
    0% {
        bottom: 4%;
    }

    50% {
        bottom: 6%;
    }

    100% {
        bottom: 4%;
    }
}

#ourclients {
    text-align: center;
    font-size: xx-large;
    padding: 3%;
    margin-top: -23vh;
}

#footersection {
    display: flex;
    box-sizing: border-box;
    padding: 3% 2%;
}

#leftfooter {
    width: 50%;
    text-align: center;
    font-size: larger;
}

#rightfooter {
    width: 50%;
    font-size: x-large;
    display: flex;
}

#emptyspace {
    width: 10%;
}

#formdiv {
    width: 90%;
}

#fstname {
    display: flex;
    flex-direction: column;
    padding-right: 5%;
    width: 35%;
}

#lstname {
    display: flex;
    flex-direction: column;
    width: 35%;
}

#footerform {
    text-align: left;
}

#footerlogo {
    width: 20%;
}

#navul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
    margin: 0;
}

#homepagelogo {
    font-size: x-large;
    height: auto;
    width: 9%;
}

#showmenu {
    width: 22%;
    visibility: hidden;
}

#usricon {
    position: relative;
    top: -1vh;
    font-size: 45px;
    color: tomato;
}

#usrname {
    position: relative;
    top: -1.4vh;
    font-size: x-large;
    margin-left: 10%;
    text-decoration: none;
    color: tomato;
}

#usericonpos {
    position: relative;
    top: 0.5vh;
}

#user {
    padding: 1%;
    min-width: 8%;
}

.imgsection {
    position: relative;
}

#imgandtext1 {
    display: flex;
    width: 100%;
}

#imgandtext2 {
    display: flex;
    width: 100%;
}

#sliderimg {
    display: flex;
}

.sliderrimg {
    width: 30vw;
    overflow: hidden;
}

.leftsection,
.rightsection {
    display: flex;
    width: 100%;
}

.middlesection {
    background-color: rgb(23, 150, 188);
    padding-bottom: 5%;
    margin-bottom: 10%;
}

#membershipmaindiv {
    display: flex;
    width: 100%;
    box-sizing: border-box;
}

#note {
    font-size: large;
    font-family: monospace;
    color: brown;
}

.subservice {
    padding: 0 10%;
    text-align: center;
    font-size: larger;
}

.subserviceBody1,
.subserviceBody {
    display: flex;
    box-sizing: border-box;
    align-items: center;
    justify-content: center;
}

#bmiimg1 {
    width: 40%;
}

#bmiimgtext1 {
    width: 60%;
}

#bfimgtext1 {
    width: 70%;
}

#bfimg1 {
    width: 30%;
}

.smalldiv {
    width: 35%;
}

.bigdiv {
    width: 65%;
}

.servicesForm {
    width: 100%;
    box-sizing: border-box;
    padding: 3% 10%;
    background-color: azure;

}

.formheading {
    text-align: center;
    font-size: larger;
}

.formbody {
    display: flex;
    box-sizing: border-box;
    padding: 0 10%;
    text-align: initial;
}

.inputfields {
    width: 40%;
    display: flex;
    box-sizing: border-box;
    justify-content: center;
    align-items: center;
}

.genderinput {
    width: 17.5%;
}

.maleinp {
    width: 11%;
}

.femaleinp {
    width: 17%;
}

.inputfield {
    display: flex;
    box-sizing: border-box;
    padding: 3.5% 10% 0;
    text-align: initial;
}

.resultsection {
    display: flex;
    box-sizing: border-box;
    align-items: center;
    justify-content: center;
    padding-top: 4%;
}

.resultText {
    width: 40%;
}

.resultGraph {
    width: 50%;
    font-size: large;
}

#strengthGraph {
    width: 40%;
    font-size: large;
}

#strengthImg {
    float: left;
    margin: 0 5% 0 0;
    width: 90%;
}

#subservices {
    position: relative;
    top: -22.5vh;
    margin: 0 5%;
    width: 90%;
    background-color: white;
}

.formsect {
    margin-top: 6vh;
}

#logintopDiv {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
    background-color: #31A9F5;
    transition: background-color 1s;
    /* background-color: #6C51FE; */
}

#innerloginDiv {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50vw;
    height: 50vh;
    background-color: #2A96D7;
    transition: background-color 1s;
    /* background-color: #6048E4; */
}

.userdetails {
    width: 50%;
    text-align: center;
    color: white;
}

.switchbutton {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    width: 40%;
    height: 120%;
    background-color: white;
}

.switchhere {
    cursor: pointer;
    font-size: x-large;
    padding: 2% 5%;
    border: none;
    background: white;
    color: black;
}

.logininputs {
    width: 70%;
    font-size: 18px;
    padding: 2% 0 2% 3%;
    margin-bottom: 6%;
}

.register {
    cursor: pointer;
    font-size: larger;
    border: none;
    padding: 3% 5%;
    color: white;
    background-color: #31A9F5;
    /* background-color: #6C51FE; */
}

.loginform {
    margin-top: 5%;
}

.placeholderColor::-webkit-input-placeholder {
    color: red;
}

#afterFeedback {
    position: absolute;
    display: none;
    left: 37%;
    animation: thanks 5s;
}

@keyframes thanks {
    0% {
        bottom: 4vh;
        opacity: 0;
    }

    30% {
        bottom: 10vh;
        opacity: 1;
    }

    70% {
        opacity: 1;
    }

    100% {
        bottom: 65vh;
        opacity: 0;
    }
}

/* for mobile view */
@media only screen and (max-width: 767px) {

    #afterFeedback {
        left: 22%;
    }

    #innerloginDiv {
        flex-direction: column-reverse;
        overflow: hidden;
        width: 100%;
        height: 100%;
    }

    #gotoSignin {
        padding-bottom: 5%;
        background-color: #2A96D7;
        font-size: small;
    }

    #signIn {
        padding: 5% 0;
    }

    #signUp {
        padding: 5% 0;
    }

    .userdetails,
    .switchbutton {
        width: 100%;
    }

    #usersection {
        display: flex;
    }

    .topsubsection {
        margin-bottom: 5%;
    }

    #subservices {
        margin: 0;
        width: 100%;
    }

    .servicelinks {
        width: auto;
        padding-top: 5%;
    }

    #strengthImg {
        width: 100%;
    }

    #strengthGraph,
    .resultGraph {
        width: 100%;
        margin-top: 7%;
    }

    .resultText {
        width: 90%;
    }

    .resultsection {
        flex-direction: column;
    }

    #happyClients {
        font-size: xx-large;
    }

    .inputfield {
        padding: 0;
        flex-direction: column;
    }

    .calculatebtn {
        padding: 3% 9%;
    }

    .femaleinp {
        text-align: center;
        width: 30%;
    }

    .genderinput {
        width: 47%;
    }

    .maleinp {
        text-align: center;
        width: 23%;
    }

    .gender {
        padding: 0 0 5%;
    }

    .inputfields {
        width: 100%;
        margin-bottom: 5%;
    }

    .formbody {
        flex-direction: column;
        padding: 0;
    }

    .formheading {
        font-size: initial;
    }

    .servicesForm {
        padding: 3% 0;
    }

    #bmiimg1,
    #bfimg1,
    #bmiimgtext1,
    #bfimgtext1,
    .bigdiv,
    .smalldiv {
        width: 100%;
    }

    .subserviceBody1 {
        flex-direction: column-reverse;
        padding: 0 5%;
    }

    .subserviceBody {
        flex-direction: column;
        padding: 0 5%;
    }

    .subservice {
        padding: 0;
        font-size: inherit;
    }

    #note {
        font-size: inherit;
    }

    .serviceNav {
        font-size: x-large;
        padding: 0 0 2vh;
        justify-content: space-around;
    }

    #membershipmaindiv {
        flex-direction: column;
    }

    .membershipcard {
        width: 100%;
        margin-bottom: 4vh;
    }

    .middlesection {
        margin-bottom: 45%;
    }

    .descparagraph {
        padding: 0 5%;
    }

    .topdivabout {
        height: 110vh;
    }

    .leftsection {
        flex-direction: column;
    }

    .rightsection {
        flex-direction: column-reverse;
    }

    .abouttitle {
        font-size: x-large;
        padding: 8vh 2vh 0;
    }

    .sliderrimg {
        width: 90vw;
    }

    #Nextbtn,
    #Prevbtn {
        display: none;
    }

    #sliderimg {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    #imgandtext2 {
        flex-direction: column-reverse;
        margin-top: 10%;
    }

    .section2nd {
        position: unset;
        transform: translate(0%, 0%);
        font-size: large;
        margin-bottom: 4%;
    }

    #imgandtext1 {
        flex-direction: column;
    }

    .imgsection {
        position: unset;
    }

    .section {
        width: 100%;
        overflow: visible;
    }

    .logonav:hover {
        background-color: transparent !important;
        color: black;
        transition: background-color 0.5s;
        border-radius: 8px;
    }

    #title1 {
        margin-left: 5vw;
        font-size: x-large;
    }

    #joinlink:hover {
        background-color: tomato;
        color: white !important;
        transition: color 0.5s, background-color 0.5s;
        border: 2px solid;
    }

    #joinlink {
        padding: 2% 5%;
    }

    #title2 {
        font-size: x-large;
        margin-left: 28vw;
        margin-top: 5vh;
    }

    #user {
        display: flex;
        justify-content: center;
        margin-top: 2.2%;
    }

    #usrname {
        margin-left: 3%;
        top: -1.5vh;
    }

    #showmenu {
        width: 72%;
        visibility: visible;
        float: right;
        text-align: end;
        position: relative;
        right: -6vh;
    }

    #usricon {
        font-size: 30px;
        top: -2.5vh;
        margin-right: 2vw;
    }

    .nav {
        width: 8% !important;
        width: 100% !important;
        margin-bottom: 1%;
    }

    #navul {
        flex-direction: column;
        position: relative;
    }

    #homepagelogo {
        text-align: start;
        margin-top: 1vh;
        margin-left: 6vw;
        width: 34%;
        position: absolute;
        left: -2vh;
    }

    #footerlogo {
        width: 40%;
    }

    #footerform {
        text-align: center;
    }

    .gotoTop {
        width: 35%;
        right: -21%;
    }

    .otherinputs {
        font-size: 16px;
        text-align: center;
    }

    .nameinputs {
        font-size: 16px;
        text-align: center;
    }

    #lstname {
        width: 49%;
    }

    #fstname {
        width: 45%;
    }

    #ourclients {
        font-size: initial;
        padding-bottom: 7%;
        margin-top: -23vh;
    }

    #footersection {
        padding-top: 7%;
        display: block;
    }

    #leftfooter {
        width: 100%;
    }

    #rightfooter {
        margin-top: 8%;
        width: 100%;
    }

    .submit {
        padding: 2% 5%;
    }

    #emptyspace {
        width: 0;
    }

    #formdiv {
        width: 100%;
    }
}






















@media screen and (max-width: 1240px) {
    .navWidth {
        text-align: center;
        width: 13%;
        height: -webkit-fill-available;
    }

    .nav {
        padding: 2% 1%;
    }

    #rightemptyli {
        width: 20%;
    }

    #user {
        width: 11%;
    }

}

@media screen and (max-width: 1640px) and (min-width: 1240px) {
    .navWidth {
        text-align: center;
        width: 10%;
        height: -webkit-fill-available;
    }

    .nav {
        padding: 2% 1%;
    }

    #rightemptyli {
        width: 20%;
    }
}